.coach {
  &__wrapper {
    position: relative;
    box-sizing: border-box; //
    min-height: 513px; //
    padding-bottom: 120px;
  }

  &__wrapper h3 {
    margin: 0 0 60px 0;

    font-family: "Bebas Neue Regular", "Arial", sans-serif;
    font-size: 49px;
    line-height: 49px;
    letter-spacing: 0.03em;
  }

  &__slider-btn {
    top: 250px;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  &__item {
    position: relative;
  }

  &__item:hover .coach__name {
    display: none;
  }

  &__info {
    position: absolute;
    display: none;
    box-sizing: border-box;
    padding: 31px;

    color: #ffffff;

    background: rgba(16, 38, 84, 0.89);
  }

  &__info-name {
    margin: 0;

    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    text-align: center;
    line-height: 150%;
  }

  &__info-subtitle {
    margin: 0 0 26px 0;

    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    text-align: center;
    line-height: 150%;
    color: #b6bdcc;
  }

  &__info-list {
    width: 159px;
  }

  &__info-list li {
    margin: 0 0 24px 0;

    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    color: #ffffff;

    list-style: disc;
  }

  &__item:hover .coach__info {
    display: block;
  }

  &__picture {
    display: block;
  }

  &__name {
    position: absolute;
    bottom: 30px;
    display: block;
    width: 100%;
    margin: 0;

    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    text-align: center;
    line-height: 150%;
    color: #ffffff;
  }
}

